.emic-btn-primary {
  background: #4cabe9;
  color: #fff;
  display: inline-block;
  text-align: center;
  cursor: pointer;
  -webkit-transition: all 0.35s cubic-bezier(0.075, 0.82, 0.165, 1);
  -o-transition: all 0.35s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: all 0.35s cubic-bezier(0.075, 0.82, 0.165, 1);
  padding: 10px 15px;
  font-size: 14px;
  line-height: 1;
  border-radius: 3px;
  border: 1px solid #4cabe9;
}
.emic-btn-primary:hover {
  background-color: rgba(76, 171, 233, 0.85);
  border: 1px solid rgba(76, 171, 233, 0.85);
}
.emic-btn-info {
  background: #909399;
