  border-radius: 3px;
  -webkit-transition: cubic-bezier(0.075, 0.82, 0.165, 1);
  -o-transition: cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: cubic-bezier(0.075, 0.82, 0.165, 1);
}
.emic-btn-info:hover {
  background-color: rgba(144, 147, 153, 0.85);
}
.emic-btn-danger {
  background: #fe4e4e;
  color: #fff;
  display: inline-block;
  text-align: center;
  cursor: pointer;
  -webkit-transition: all 0.35s cubic-bezier(0.075, 0.82, 0.165, 1);
  -o-transition: all 0.35s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: all 0.35s cubic-bezier(0.075, 0.82, 0.165, 1);
  padding: 10px 15px;
  font-size: 14px;
  line-height: 1;
  border-radius: 3px;
  -webkit-transition: cubic-bezier(0.075, 0.82, 0.165, 1);
  -o-transition: cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: cubic-bezier(0.075, 0.82, 0.165, 1);
}
.emic-btn-danger:hover {
  background-color: rgba(254, 78, 78, 0.85);
}
.emic-btn-plain {
  background-color: rgba(76, 171, 233, 0.2);
  border: 1px solid #4cabe9;
  color: #4cabe9;
}
.emic-btn-plain:hover {
  background-color: #4cabe9;
  border: 1px solid #4cabe9;
  color: #fff;
}



