.emic-modal-wrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  visibility: hidden;
}
.emic-modal-wrap.show {
  visibility: visible;
}
.emic-modal-wrap.show .emic-modal-mask {
  opacity: 1;
}
.emic-modal-wrap.show .emic-modal-content {
  top: 30%;
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}
.emic-modal-mask {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.65);
  opacity: 0;
  -webkit-transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  -o-transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.emic-modal-content {
  -webkit-transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  -o-transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  position: relative;
  z-index: 1001;
  margin: 0 auto;
  top: 0;
  background-color: #fff;
  border-radius: 10px;
  opacity: 0;
  -webkit-transform: scale(0.5);
      -ms-transform: scale(0.5);
          transform: scale(0.5);
}
.emic-modal-header,
.emic-modal-footer,
.emic-modal-body {
  padding: 16px 16px;
}
.emic-modal-header {
  position: relative;
}
.emic-modal-header .icon-close {
  cursor: pointer;
  right: 16px;
  position: absolute;
  top: 50%;
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
}
.emic-modal-header .icon-close:hover {
  color: #4cabe9;
}
.emic-modal-footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.emic-modal-footer button + button {
  margin-left: 15px;
}



